import * as React from "react";
import style from "./Home.module.scss";
import {useLayoutEffect, useRef} from "react";
import gsap from "gsap";
import {BuyNow} from "./BuyNow";
import {Links} from "./Links";
import {FormCustom} from "./FormCustom";
import {Spam} from "./Spam";
import {WatchVideo} from "./WatchVideo";

export const Home = () => {
    const appRef = useRef<HTMLDivElement>(null!);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {

            gsap.timeline({repeat: -1})
                .set(".buyNow", {scaleX: 0.75, scaleY: 1})
                .to(".buyNow", {scaleX: 1, scaleY: 0.75, duration: 2})
                .to(".buyNow", {scaleX: 0.75, scaleY: 1, duration: 2})

            gsap.fromTo(".link0", {
                rotate: -15
            }, {
                rotate: 15,
                repeat: -1,
                duration: 2,
                yoyo: true,

            })

            gsap.fromTo(".link1", {
                rotate: 15
            }, {
                rotate: -15,
                repeat: -1,
                duration: 2,
                yoyo: true,
            });

            gsap.fromTo(".link2", {
                rotate: 15
            }, {
                rotate: -15,
                repeat: -1,
                duration: 2,
                yoyo: true,
                delay: 0.5
            });

            gsap.to(".form", {
                scale: 1.05,
                repeat: -1,
                duration: 2,
                yoyo: true,
            });

            gsap.timeline({ repeat: -1 })
                .set(".popup_1", { x: -2, y: -2, duration: 0.2 })
                .to(".popup_1", { x: 2, y: -2, duration: 0.2 })
                .to(".popup_1", { x: 2, y: 2, duration: 0.2 })
                .to(".popup_1", { x: -2, y: 2, duration: 0.2 })
                .to(".popup_1", { x: -2, y: -2, duration: 0.2 })

            gsap.timeline({ repeat: -1, delay: 0.4 })
                .set(".popup_3", { x: -2, y: -2, duration: 0.2 })
                .to(".popup_3", { x: 2, y: -2, duration: 0.2 })
                .to(".popup_3", { x: 2, y: 2, duration: 0.2 })
                .to(".popup_3", { x: -2, y: 2, duration: 0.2 })
                .to(".popup_3", { x: -2, y: -2, duration: 0.2 })

            gsap.timeline({repeat: -1})
                .set(".popup_2", {scaleX: 0.9, scaleY: 1})
                .to(".popup_2", {scaleX: 1, scaleY: 0.9, duration: 0.1})
                .to(".popup_2", {scaleX: 0.9, scaleY: 1, duration: 0.1})

            gsap.timeline({ repeat: -1 })
                .to(".smile", { duration: 0.1 })
                .set(".smile", { opacity: 0 })
                .to(".smile", { duration: 0.1 })
                .set(".smile", { opacity: 1 })

            gsap.timeline({ repeat: -1 })
                .to(".btn", { duration: 0.1 })
                .set(".btn", { opacity: 0 })
                .to(".btn", { duration: 0.1 })
                .set(".btn", { opacity: 1 })

        }, appRef);
        return () => ctx.revert();
    }, [])

    return (
        <div className={style.home}
             ref={appRef}
        >
            <div className={style.mobileContent}>
                <BuyNow/>
                <Links/>
                <FormCustom/>
                <Spam/>
                <WatchVideo/>
            </div>

            <div className={style.tabletContent}>
                <Links/>
                <WatchVideo/>
                <div>

                </div>
            </div>

            <div className={style.desktopContent}>
                <Links/>
                <div className={style.rightPart}>
                    <div className={style.topRow}>
                        <BuyNow/>
                        <div className={style.topRow_right}>
                            <div>Socials</div>
                            <FormCustom/>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}









